@import url(https://fonts.googleapis.com/css?family=Josefin+Sans|ZCOOL+KuaiLe);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #21252B;
  color: #fcfdfe; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.page {
  height: 100vh;
  width: 100%; }

#frontpage {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column; }
  #frontpage .center {
    text-align: center;
    padding-bottom: 1rem; }
  #frontpage .title {
    color: #73C990;
    font-family: 'ZCOOL KuaiLe', cursive;
    font-size: 3rem;
    padding-bottom: .8rem; }

#webdev {
  background-color: #282C34;
  display: -webkit-flex;
  display: flex; }
  #webdev .hljs {
    font-size: .8rem;
    overflow: hidden; }
  #webdev .left {
    width: 28%;
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
    position: relative;
    z-index: 2; }
    #webdev .left::after {
      position: absolute;
      content: '';
      right: 0;
      top: 0;
      bottom: 0;
      width: 3rem;
      background: -webkit-linear-gradient(left, rgba(40, 44, 52, 0) 0%, #282c34 80%, #282c34 100%);
      background: linear-gradient(to right, rgba(40, 44, 52, 0) 0%, #282c34 80%, #282c34 100%); }
  #webdev .right {
    -webkit-flex-shrink: 1;
            flex-shrink: 1;
    background-color: #ffffff;
    width: 100%; }

/*

Atom One Dark by Daniel Gamage
Original One Dark Syntax theme from https://github.com/atom/one-dark-syntax

base:    #282c34
mono-1:  #abb2bf
mono-2:  #818896
mono-3:  #5c6370
hue-1:   #56b6c2
hue-2:   #61aeee
hue-3:   #c678dd
hue-4:   #98c379
hue-5:   #e06c75
hue-5-2: #be5046
hue-6:   #d19a66
hue-6-2: #e6c07b

*/

.hljs {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  color: #abb2bf;
  background: #282c34;
}

.hljs-comment,
.hljs-quote {
  color: #5c6370;
  font-style: italic;
}

.hljs-doctag,
.hljs-keyword,
.hljs-formula {
  color: #c678dd;
}

.hljs-section,
.hljs-name,
.hljs-selector-tag,
.hljs-deletion,
.hljs-subst {
  color: #e06c75;
}

.hljs-literal {
  color: #56b6c2;
}

.hljs-string,
.hljs-regexp,
.hljs-addition,
.hljs-attribute,
.hljs-meta-string {
  color: #98c379;
}

.hljs-built_in,
.hljs-class .hljs-title {
  color: #e6c07b;
}

.hljs-attr,
.hljs-variable,
.hljs-template-variable,
.hljs-type,
.hljs-selector-class,
.hljs-selector-attr,
.hljs-selector-pseudo,
.hljs-number {
  color: #d19a66;
}

.hljs-symbol,
.hljs-bullet,
.hljs-link,
.hljs-meta,
.hljs-selector-id,
.hljs-title {
  color: #61aeee;
}

.hljs-emphasis {
  font-style: italic;
}

.hljs-strong {
  font-weight: bold;
}

.hljs-link {
  text-decoration: underline;
}

